				"html": "<p>The <code>Math.min()</code> function returns the smallest of zero or more numbers.</p>\n<h2>Syntax</h2>\n<pre><code>Math.min(value1, value2, ...)\n</code></pre>\n<h3>Parameters</h3>\n<p><code>value1</code>, <code>value2</code> and other arguments should be numbers. All arguments are optional.</p>\n<p>If no arguments are given, the result is Infinity. If at least one of arguments cannot be converted to a number, the result is NaN.</p>\n<p><a href=\"https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Global_Objects/Math/min\">MDN link</a> | <a href=\"https://msdn.microsoft.com/en-us/LIBRary/7x8968dh(v=vs.94).aspx\">MSDN link</a></p>\n<h2>Examples</h2>\n<pre><code>Math.min();         // Infinity\nMath.min('a');      // NaN\nMath.min(NaN);      // NaN\nMath.min(5);        // 5\nMath.min(-1, 10);   // -1\n\nvar x = Math.min(107 - 3,  48 * 90);\ndocument.write(x);\n\n// Output:\n// 104\n</code></pre>",
